.about {
  &__title {
    font-weight: 500;
    font-size: 32px;
    line-height: 100%;
    letter-spacing: -1.5px;
    color: $c-white;

    @include onTablet {
      margin-top: 0;
      font-size: 48px;
    }

    @include onDesktop {
      font-size: 64px;
    }
  }

  &__title-link {
    color: $c-dark;
    text-decoration: none;
  }

  &__subtitle {
    font-weight: 500;
    font-size: 23px;
    line-height: 140%;
    color: $c-white;
  }
}